                    display: none;
                }
                &::after{
                    display: none;
                }
            }
            .footer-list {
                li {
                    a {
                        font-size: 14px;
                    }
                }
            }
            .foot-social {
                left: 0;
            }
        }
    }
    .copy-area {
        margin-top: 20px !important;
    }
    .footer-area {
        .copy-area {
