                .slider-btn .box-btn {
                    margin-right: 0;
                    margin: 0 8px;
                }
            }
        }
        .owl-nav {
            .owl-next {
                width: 40px;
                height: 40px;
                font-size: 15px;
                line-height: 40px;
                top: unset;
                bottom: -30px;
                right: 10%;
                
            }
            .owl-prev {
                width: 40px;
                height: 40px;
                font-size: 15px;
                line-height: 40px;
                top: unset;
                bottom: -30px;
                left: 10%;
                
            }
        }
    }
    .technology-area {
        .technology-content {
            h2 {
