        height: 30px;
        line-height: 30px;
    }
    .header-area {
        .header-content-right {
            float: unset;
        }
        .col-lg-8.col-sm-8.text-end.pr-0 {
            text-align: center !important;
            padding-right: 15px !important;
        }
        .col-lg-2.col-sm-4.text-end.pl-0 {
            text-align: center !important;
        }
    }
    .header-area.header-2 {
        .header-content-right {
            float: unset;
        }
