    .footer-area {
        .copy-area {
            p {
                font-size: 14px;
            }
        }
    }
    .slider-area-2 {
        .home-slider {
            .single-slider {
                height: 100%;
                padding-top: 140px;
                padding-bottom: 100px;
            }
        }
    }
    .home2-choose-area {
        .single-choose {
            h3{
                font-size: 19px;
            }
        }
    }
    .main-banner-three {
        .main-banner-content {
            .content {
                max-width: 450px;
                h1 {
                    font-size: 50px;
                }
            }
        }
    }
}

/* Max width 1800px */
@media only screen and (min-width: 1800px) {
    .banner-area {
        height: 100vh;
        .home-shape {
            .shape2 {
                top: 18%;
                left: 16%;
            }
        }
    }
    .home-team-slider-area {
        .team-right {
            height: 100%;
            .single-team {
                padding-left: 40px;
                .content {
                    p {
                        padding: 0 160px;
                    }
                }
            }
        }
    }
}

/* Max width 991px Only Mobile Devices */
@media only screen and (max-width: 991px) {
    
    .navbar-area {
        border-bottom: 1px solid #242424;

        .main-nav {
            position: relative !important;
            top: 0 !important;
            background-color: var(--darkBlueColor);

            .navbar {
                position: relative;
    
                .collapse {
                    margin-top: 15px;
                    max-height: 70vh;
                    overflow-y: scroll;
                }
                .navbar-nav {
                    .nav-item {
                        .dropdown-menu {
                            position: relative;
                            top: 0;
                            width: 100%;
                            opacity: 1;
                            visibility: visible;
                            margin-top: 18px;
                        }
                    }
                }
            }
        }
        .navbar-brand {
            &.d-none {
                display: inline-block !important;
            }
        }
        .nav-right {
            display: none;
        }
        .nav-btn {
            display: none;
        }
        .navbar-toggler {
            color: var(--whiteColor);
            font-size: inherit;
            box-shadow: unset;
            border: none;
            padding: 0;

            .burger-menu {
                cursor: pointer;

                span {
                    height: 2px;
                    width: 30px;
                    margin: 6px 0;
                    display: block;
                    background: var(--whiteColor);
                }
            }
        }
        .active {
            .navbar-toggler {
                .burger-menu {
                    span {
                        &.top-bar {
                            transform: rotate(45deg);
                            transform-origin: 10% 10%;
                        }
                        &.middle-bar {
                            opacity: 0;
                        }
                        &.bottom-bar {
                            transform: rotate(-45deg);
                            transform-origin: 10% 90%;
                            margin-top: 5px;
                        }
                    }
                }
            }
            .collapse:not(.show) {
                display: block;
            }
        }
    }

}